.icon {
	border-radius: 50%;
	position: relative;
	left: 50%;
	width: 10vh;
	height: 10vh;
}

.imageFrame {
	margin-top: 10vh;
	width: 20vh;
	height: 20vh;
	border-radius: 50%;
	background-color: white;
}

.image {
	border-radius: 50%;
	width: 20vh;
	height: 20vh;
	margin-left: auto;
	margin-right: auto;
	display: inline-block;
	padding: 2vh;
}

.background {
	margin: 0;
	background-image: url('../../images/background2.jpg');
	opacity: .9;
}

.text {
	font-family: monospace;
	position: relative;
}

.col {
	margin-bottom: 5vh;
}

.row {
	margin: 0
}

#title {
	color: white;
}

